import edit_input from "../../../../edit_input";
import f_button from "../../../../button";

export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "审核",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "维修人", k: "张三" },
        { read: true, v: "开始时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "结束时间", k: "2019-01-12 09:00:12" }
      ],
      edit_input_list1: [
        { read: true, v: "设备名称", k: "钢轨廓形仪" },
        { read: true, v: "固资编号", k: "M322424" },
        { read: true, v: "故障描述", k: "描述内容" },
        { read: true, v: "故障责任人", k: "张三" },
        { read: true, v: "维修单位", k: "单位名称" },
        { read: true, v: "预估金额", k: "400" },
        { read: true, v: "仓管员", k: "张三" }
      ],
      edit_input_list2: [
        { read: true, v: "设备状态", k: "正常" },
        { read: true, v: "维修金额", k: "400" },
        { read: true, v: "仓管员", k: "张三" }
      ],
      edit_input_list3: [
        { read: true, v: "开始时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "备注", k: "备注信息" }
      ],
      edit_input_list4: [
        { read: true, v: "结束时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "备注", k: "备注信息" }
      ],
      edit_input_list5: [
        { read: true, v: "创建时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "最后更新", k: "2019-01-12 09:00:12" },
      ],
    };
  },
  methods: {
    get_call_back() {
    //   this.go_page("/device_details");
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



