import edit_input from "../../../../edit_input";
import f_table from "../../../../table";

export default {
  components: {
    edit_input,
    f_table
  },
  data() {
    return {
      edit_input_list: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "出库人", k: "张三" },
        { read: true, v: "出库时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "备注", k: "备注信息" }
      ],
      edit_input_list1: [
        { read: true, v: "出库单位", k: "事业部" },
        { read: true, v: "接收部门", k: "北方事业部" },
        { read: true, v: "备注", k: "京广高铁工务段信阳东站跟车作业" }
      ],
      edit_input_list2: [
        { read: true, v: "创建时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "最后更新", k: "2019-01-12 09:00:12" }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "序号", min_w: true },
                { td: "设备名称",  },
                { td: "固资编号" }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "轨道检测仪" },
                { td: "My34234" }
              ]
            },
            {
              td_list: [
                { td: "10000", min_w: true },
                { td: "轨道检测仪" },
                { td: "My34234" }
              ]
            },
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "轨道检测仪" },
                { td: "My34234" }
              ]
            }
          ]
        }
      }
    };
  },
  methods: {
    get_call_back() {
      //   this.go_page("/device_details");
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



