import list_item from "@/components/list_item";
import f_button from "@/components/button";
import f_search from "@/components/search";
import f_nav_bar from "@/components/nav_bar";
import actionsheet from "@/components/actionsheet";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    f_search,
    f_nav_bar,
    actionsheet
  },
  data() {
    return {
      actionsheet_info: {
        show: false,
        list: {
          menu1: "维修",
          menu2: "状态编辑",
          menu3: "取消"
        },
        call_back: true
      },
      search: {
        placeholder: "请输入要盘点的日期"
      },
      button_list: [
        {
          type: "primary",
          text: "状态修改",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            cur: true,
            text: "状态管理列表"
          }
        ]
      },

      list: [
        {
          title: "2019-01-05",
          type: "开始维修",
          time: "2019-01-05 12:12:21",
        },
        {
          title: "2019-01-05",
          type: "结束维修",
          time: "2019-01-05 12:12:21",
        },
        {
          title: "2019-01-05",
          type: "报废",
          time: "2019-01-05 12:12:21",
          hide_border:true,
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.actionsheet_info.show = true;
    },

    go_page(url) {
      this.$router.push(url);
    }
  }
};



