import top_progress from "../../../top_progress";
import edit_input from "../../../edit_input";
import edit_select from "../../../edit_select";
import edit_time from "../../../edit_time";
import edit_radio from "../../../edit_radio";
import edit_textarea from "../../../edit_textarea";
import f_button from "../../../button";
import nav_bar from "../../../nav_bar";
import loading from "../../../loading";
import result from "../../../result";
import f_confirm from "../../../confirm";
export default {
  name: "online_report",
  components: {
    top_progress,
    edit_input,
    edit_select,
    edit_time,
    edit_radio,
    edit_textarea,
    f_button,
    nav_bar,
    loading,
    result,
    f_confirm
  },
  data() {
    return {
      top_progress: {
        step: 0,
        list: [{ des: "今日工作" }, { des: "工作明细" }, { des: "填报完成" }]
      },
      top_title: "实际工作",
      edit_select: [
        {
          title: "打卡记录",
          default_val: "",
          required: true,
          placeholder: "请选择打卡记录",
          list: [
            { key: "打卡1", value: "打卡1" },
            { key: "打卡2", value: "打卡2" }
          ]
        }
      ],
      edit_select1: [
        {
          title: "工作类型",
          default_val: "途中",
          required: true,
          placeholder: "",
          list: [
            { key: "途中", value: "途中" },
            { key: "办公室", value: "办公室" }
          ]
        },
        {
          title: "工作路局",
          default_val: "",
          required: true,
          placeholder: "请选择工作路局",
          list: [{ key: "武汉", value: "武汉" }, { key: "北京", value: "北京" }]
        },
        {
          title: "工作地点",
          default_val: "",
          required: true,
          hide_border:true,
          placeholder: "请选择工作地点",
          list: [{ key: "武汉", value: "武汉" }, { key: "北京", value: "北京" }]
        }
      ],
      edit_time_list: [
        { title: "上线时间", default_time: "请选择上线时间" },
        { title: "下线时间", default_time: "请选择下线时间" }
      ],
      button_list: [
        {
          type: "primary",
          text: "下一步",
          call_back: true
        }
      ],
      edit_input_list: [{ v: "团队人员", k: "请输入团队成员" }],
      edit_textarea: {
        title: "备注",
        placeholder: "请输入备注信息"
      },
      step_2_nav_bar_idx: 0,
      loading_info: {
        show: false,
        text: "日报提交,请等待.."
      },
      result_info: {
        title: "操作成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>的日报提交成功。',
        buttons: [
          {
            type: "primary",
            text: "重新编辑",
            link:'BACK',
          },
          {
            type: "primary",
            text: "新日报",
            link:'/report_today'
          },
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      },
      confirm_info:{
        show:false,
        title:'提交日报',
        content:'是否确定填写无误，提交日报'
      }
    };
  },
  methods: {
    get_call_back(res) {
      var step = this.top_progress.step;
      if (step == 0) {
        this.top_progress.step = 1;
        this.top_title = "工作内容";
        this.go_top();
        this.button_list = [
          {
            type: "primary",
            text: "提交日报",
            call_back: true,
          }
        ];
      }else if(step==1){
        this.confirm_info.show = true;
      }
    },
    
    go_top() {
      window.scroll(0, 0);
    },
    get_nav_bar(idx) {
      if (idx == 1) {
        this.edit_input_list2 = [
          {
            v: "其他",
            k: "其他"
          }
        ];
      } else {
        this.edit_input_list2 = [{ v: "车轮测量个数", k: "请输入车轮个数" }];
      }
    },
    confirm_back(val) {
      this.loading_info.show = true;
      setTimeout(() => {
        this.loading_info.show = false;
        this.top_progress.step = 2;
      }, 1000);
    }
  }
};



