import edit_select from "../../../edit_select";
import edit_input from "../../../edit_input";
import edit_textarea from "../../../edit_textarea";
import f_confirm from "../../../confirm";
import loading from "../../../loading";
import edit_time from "../../../edit_time";
import f_button from "../../../button";
import edit_radio from "../../../edit_radio";
import result from "../../../result";
export default {
  components: {
    edit_select,
    edit_textarea,
    edit_input,
    f_button,
    loading,
    f_confirm,
    result,
    edit_time,
    edit_radio
  },
  data() {
    return {
      show_result: false,
      edit_input_list: [
        { v: "详细位置", k: "请输入详细位置",required:true},
      ],
      edit_time_list: [
        { title: "上线时间", default_time: "请选择上线时间",required:true},
        { title: "下线时间", default_time: "请选择下线时间",required:true}
      ],
      edit_radio: { title: "是否天窗", required: true, list: ["是", "否"] },
      edit_select: [
        {
          title: "工作类型",
          default_val: "",
          required: true,
          placeholder: "请选择工作类型",
          list: [
            { key: "办公室作业", value: "办公室作业" },
            { key: "办公室作业1", value: "办公室作业1" }
          ]
        },
        {
          title: "工作路局",
          default_val: "",
          required: true,
          placeholder: "请选择工作路局",
          list: [
            { key: "武汉", value: "武汉" },
            { key: "武汉1", value: "武汉1" }
          ]
        },
        {
          title: "工作地点",
          default_val: "",
          required: true,
          placeholder: "请选择工作地点",
          list: [
            { key: "武汉", value: "武汉" },
            { key: "武汉1", value: "武汉1" }
          ]
        }
      ],
      edit_textarea: {
        title: "备注",
        required: true,
        placeholder: "请输入备注"
      },
      button_list: [
        {
          type: "primary",
          text: "提交次日计划",
          call_back: true
        }
      ],
      loading_info: {
        show: false,
        text: "日报提交,请等待.."
      },
      confirm_info: {
        show: false,
        title: "提交日报",
        content: "是否确定填写无误，提交日报"
      },
      result_info: {
        title: "操作成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>的日报提交成功。',
        buttons: [
          {
            type: "primary",
            text: "重新编辑",
            link: "BACK"
          },
          {
            type: "primary",
            text: "新计划",
            link: "/report_today"
          },
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    btn_call_back() {
      this.confirm_info.show = true;
    },
    confirm_back() {
      this.loading_info.show = true;
      setTimeout(() => {
        this.loading_info.show = false;
        this.show_result = true;
      }, 1000);
    }
  }
};



