import f_button from "../../../../button";
import edit_select from "../../../../edit_select";
import edit_textarea from "../../../../edit_textarea";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
import f_table from "../../../../table";
export default {
  components: {
    top_progress,
    f_button,
    edit_select,
    edit_textarea,
    f_confirm,
    f_table,
    result
  },
  data() {
    return {
      nav_bar_idx: -1,
      top_title: "出库位置",
      top_progress: {
        step: 0,
        list: [{ des: "出库位置" }, { des: "出库明细" }, { des: "出库完成" }]
      },
      edit_textarea: {
        required: true,
        title: "备注",
        placeholder: "请输入备注信息"
      },
      button_list: [
        {
          type: "primary",
          text: "下一步",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "出库单位",
          default_val: "",
          required: true,
          placeholder: "请选择出库单位",
          list: [
            { key: "事业部", value: "事业部" },
            { key: "事业部1", value: "事业部1" }
          ]
        },
        {
          title: "接受部门",
          default_val: "",
          required: true,
          placeholder: "请选择接受部门",
          list: [
            { key: "事业部", value: "事业部" },
            { key: "事业部1", value: "事业部1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "序号" ,min_w:true},
                { td: "设备名称", col: "3" },
                { td: "固资编号" }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border:true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "1",min_w:true }, { td: "轨道检测仪" }, { td: "My34234" }]
            },
            {
              td_list: [
                { td: "10000" ,min_w:true},
                { td: "轨道检测仪" },
                { td: "My34234" }
              ]
            },
            {
              td_list: [{ td: "1",min_w:true }, { td: "轨道检测仪" }, { td: "My34234" }]
            }
          ]
        }
      },
      confirm_info: {
        title: "设备出库",
        content: "设备出库成功，是否提交保存？",
        show: false
      },
      result_info: {
        title: "提交成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>的车轮廓形仪新增成功。',
        buttons: [
          {
            type: "primary",
            text: "继续出库",
            link: "/"
          },
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    get_call_back() {
      var step = this.top_progress.step;
      if (step == 0) {
        this.top_progress.step = 1;
        this.top_title = "选择出库明细";
        this.button_list[0].text = "出库";
      } else if (step == 1) {
        this.confirm_info.show = true;
      }
    },
    confirm_back(res) {
      this.top_progress.step = 2;
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



