import edit_input from "../../../../edit_input";
import f_button from "../../../../button";

export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "查看设备详情",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "入库人", k: "张三" },
        { read: true, v: "入库时间", k: "2019-01-12 09:12:12" },
        { read: true, v: "备注", k: "备注信息" }
      ],
      edit_input_list1: [
        { read: true, v: "设备名称", k: "轨道检测仪" },
        { read: true, v: "固资编号", k: "M7676720001" },
      ],
      edit_input_list2: [
        { read: true, v: "创建时间", k: "2019-01-12 09:23:12" },
        { read: true, v: "最后更新", k: "2019-01-12 09:23:12" },
      ]
    };
  },
  methods: {
    get_call_back() {
      this.go_page('/device_details')
    },
    go_page(url){
      this.$router.push(url)
    }
  }
};



