import f_button from "../../../../button";
import edit_select from "../../../../edit_select";
import tabbar from "@/components/tabbar.vue";
import edit_textarea from "../../../../edit_textarea";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
import f_table from "../../../../table";
export default {
  components: {
    top_progress,
    f_button,
    edit_select,
    edit_textarea,
    f_confirm,
    f_table,
    result,
    tabbar
  },
  data() {
    return {
      nav_bar_idx: -1,
      show_result: false,
      top_title: "出库位置",
      top_progress: {
        step: 0,
        list: [{ des: "出库位置" }, { des: "出库明细" }, { des: "出库完成" }]
      },
      edit_textarea: {
        required: true,
        title: "备注",
        placeholder: "请输入备注信息"
      },
      button_list: [
        {
          type: "primary",
          text: "领用",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "出库单位",
          default_val: "",
          required: true,
          placeholder: "请选择出库单位",
          list: [
            { key: "事业部", value: "事业部" },
            { key: "事业部1", value: "事业部1" }
          ]
        },
        {
          title: "接受部门",
          default_val: "",
          required: true,
          placeholder: "请选择接受部门",
          list: [
            { key: "事业部", value: "事业部" },
            { key: "事业部1", value: "事业部1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "序号" }, { td: "设备名称" }, { td: "数量" }]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "1" }, { td: "轨道检测仪" }, { td: "2" }]
            }
          ]
        }
      },
      table_info2: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "轨道检测仪", clo: 3 }]
            },
            {
              td_list: [{ td: "轨道检测仪" }]
            }
          ]
        }
      },
      confirm_info: {
        title: "设备出库",
        content: "设备出库成功，是否提交保存？",
        show: false
      },
      result_info: {
        title: "领用成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>，您已经成功领用2台设备',
        buttons: [
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    get_call_back() {
      this.show_result = true;
    },
    confirm_back(res) {
      this.top_progress.step = 2;
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



