import edit_input from "../../../../edit_input";

export default {
  components: {
    edit_input
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "查看设备详情",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "设备名称", k: "钢轨廓形仪" },
        { read: true, v: "设备序列", k: "M0192123" },
        { read: true, v: "责任人", k: "北一" },
        { read: true, v: "类型", k: "领用" }
      ],
      edit_input_list1: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "领用人", k: "张三" },
        { read: true, v: "领用时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "仓管员", k: "王三" },
        { read: true, v: "备注", k: "备注信息" }
      ],
      edit_input_list2: [
        { read: true, v: "编号", k: "EO1809231" },
        { read: true, v: "归还人", k: "张三" },
        { read: true, v: "归还时间", k: "2019-01-12 09:00:12" },
        { read: true, v: "归还状态", k: "正常" },
        { read: true, v: "仓管员", k: "王三" }
      ],
      edit_input_list3: [{ read: true, v: "备注", k: "备注信息" }]
    };
  },
  methods: {
    go_page(url) {
      this.$router.push(url);
    }
  }
};



