import list_item from "@/components/list_item";
import f_button from "@/components/button";
import f_confirm from "@/components/confirm";
import f_picker from "@/components/picker";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    f_confirm,
    f_picker
  },
  data() {
    return {
      confirm_info: {
        show: false,
        title: "提交日报",
        content: "您的团队成员已经录入日报，点击确定后到导入日报内容。"
      },
      select_arr: [],
      button_list: [
        {
          type: "primary",
          text: "新建作业日报",
          call_back: true
        }
      ],
      actionsheet_info1: {
        show: false,
        list: {
          "title.noop": "---选择日报类型---",
          menu1: "上线日报",
          menu2: "非上线日报",
          menu3: "取消"
        },
        call_back: true
      },
      actionsheet_info2: {
        show: false,
        list: {
          "title.noop": "",
          menu1: "车轮调查",
          menu2: "线路调查",
          menu3: "道岔调查",
          menu4: "线路跟车",
          menu5: "道岔跟车",
          menu6: "取消"
        },
        call_back: true
      },
      actionsheet_info2_1: {
        show: false,
        list: {
          "title.noop": "",
          menu1: "途中",
          menu2: "宾馆滞留",
          menu3: "办公室",
          menu4: "培训学习",
          menu5: "走访",
          menu6: "取消"
        },
        call_back: true
      },
      list: [
        {
          title: "01月12日计划",
          type: "跟车作业",
          time: "2019-11-12 12:12:21",
          status: "未完成",
          plan_text: "上",
          status_color: "red",
          plan_color: "#199ed8"
        },
        {
          title: "01月12日计划",
          type: "走访",
          time: "2019-11-12 12:12:21",
          status: "已完成",
          plan_text: "上",
          status_color: "blue",
          plan_color: "#199ed8"
        },
        {
          title: "01月12日计划",
          type: "跟车作业",
          time: "2019-11-12 12:12:21",
          status: "未完成",
          status_color: "red",
          plan_text: "非",
          plan_color: "#008000",
          hide_border: true
        }
      ],
      picker_info: {
        columns: 3,
        show: false,
        list: [
          {
            name: "新填报",
            value: "new",
            parent: 0
          },
          {
            name: "补录",
            value: "old",
            parent: 0
          },
          {
            name: "上线日报",
            value: "on",
            parent: "new"
          },
          {
            name: "非上线日报",
            value: "off",
            parent: "new"
          },
          {
            name: "上线日报",
            value: "on",
            parent: "old"
          },
          {
            name: "非上线日报",
            value: "off",
            parent: "old"
          },
          {
            name: "车轮调查",
            value: "on_1",
            parent: "on"
          },
          {
            name: "线路调查",
            value: "on_2",
            parent: "on"
          },
          {
            name: "道岔调查",
            value: "on_3",
            parent: "on"
          },
          {
            name: "线路跟车",
            value: "on_4",
            parent: "on"
          },
          {
            name: "道岔验车",
            value: "on_5",
            parent: "on"
          },
          {
            name: "小机指导",
            value: "on_6",
            parent: "on"
          },
          {
            name: "线路验收",
            value: "on_7",
            parent: "on"
          },
          {
            name: "道岔跟车",
            value: "on_8",
            parent: "on"
          },
          {
            name: "线路观测",
            value: "on_9",
            parent: "on"
          },
          {
            name: "道岔观测",
            value: "on_10",
            parent: "on"
          },
          {
            name: "全项目检测",
            value: "on_11",
            parent: "on"
          },
          {
            name: "添乘",
            value: "on_12",
            parent: "on"
          },
          {
            name: "预评估",
            value: "on_13",
            parent: "on"
          },
          {
            name: "途中",
            value: "off_1",
            parent: "off"
          },
          {
            name: "宾馆滞留",
            value: "off_2",
            parent: "off"
          },
          {
            name: "办公室",
            value: "off_3",
            parent: "off"
          },
          {
            name: "培训学习",
            value: "off_4",
            parent: "off"
          },
          {
            name: "走访",
            value: "off_5",
            parent: "off"
          }
        ]
      }
    };
  },
  methods: {
    btn_call_back(res) {
      this.picker_info.show = true;
    },
   picker_back(arr){
     console.log('picker选择的内容',arr)
     if(arr[1]=="上线日报"){
       this.go_page('/online_report')
     }else{
       this.go_page('/offline_report')
     }
   },
    confirm_back(res) {
      console.log("confirm点击确定按钮", res);
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



