
import list_item from "../../../../list_item";
import edit_time from "../../../../edit_time";
import f_search from "../../../../search";
import work_bench_item from "../../../../work_bench_item";
import f_table from "../../../../table";
import Popup from 'vux/src/components/popup/index.vue'

import edit_select from "../../../../edit_select";
export default {
name: "report_today",
components: {
list_item,
f_search,
work_bench_item,
f_table,
Popup,
edit_time,
edit_select
},
data() {
return {
slider_bar_show: false,
search: {
placeholder: "请输入要搜索的设备"
},
edit_time_list: [
{ title: "领用开始日期", default_time: "请选择起始日期" },
{ title: "领用结束日期", default_time: "请选择结束日期" }
],
edit_radio: { title: "盘点状态", list: ["已盘点", "未盘点"] },
edit_select: [
{
title: "设备来源",
default_val: "",
placeholder: "请选择设备来源",
list: [
{ key: "设备", value: "设备" },
{ key: "设备1", value: "设备1" }
]
},
{
title: "设备状态",
default_val: "",
placeholder: "请选择设备状态",
hide_border:true,
list: [
{ key: "正常", value: "正常" },
{ key: "正常1", value: "正常1" }
]
}
],
table_info: {
show_border: true,
table: {
tr_list: [
{
td_list: [
{ td: "序列名称" },
{ td: "总数量" },
{ td: "领用数量" },
{ td: "库存数量" },
{ td: "维修数量" }
]
},
{
td_list: [
{ td: "钢轨廓形仪",link:'/warehouse_details_list'},
{ td: "25" ,link:'/warehouse_details_list'},
{ td: "12" ,link:'/warehouse_details_list'},
{ td: "13" ,link:'/warehouse_details_list'},
{ td: "2" ,link:'/warehouse_details_list'}
]
},
{
td_list: [
{ td: "钢轨廓形仪",link:'/warehouse_details_list' },
{ td: "25",link:'/warehouse_details_list' },
{ td: "12",link:'/warehouse_details_list' },
{ td: "13",link:'/warehouse_details_list' },
{ td: "2" ,link:'/warehouse_details_list'}
]
}
]
}
}
};
},
methods: {
call_back(res) {
this.go_page("/add_device_out");
},

go_page(url) {
this.$router.push(url);
},
reset_silder() {},
filter_silder() {},
nav_bar_back(idx) {
this.nav_bar_idx = idx;
}
}
};



