import work_bench_item from "../../../work_bench_item";
export default {
  name: "device_menagement",
  components: {
    work_bench_item
  },
  data() {
    return {
      list: [
        {
          text: "入库",
          src: require("../../../../assets/images/3_03.png"),
          link: "/warehouse_in"
        },
        {
          text: "出库",
          src: require("../../../../assets/images/3_03.png"),
          link: "/warehouse_out"
        },
        {
          text: "盘点",
          src: require("../../../../assets/images/3_03.png"),
          link: "/check_list",
          hide_border:true
        }
      ],
      list1: [
        {
          text: "领用",
          src: require("../../../../assets/images/3_03.png"),
          link: "/use_list"
        },
        {
          text: "归还",
          src: require("../../../../assets/images/3_03.png")
        },
        {
          text: "我的盘点",
          src: require("../../../../assets/images/3_03.png")
        },
        {
          text: "我的领用",
          src: require("../../../../assets/images/3_03.png"),
           hide_border:true
        }
      ],
      list2: [
        {
          text: "审核",
          src: require("../../../../assets/images/1_15.png")
        },
        {
          text: "状态管理",
          src: require("../../../../assets/images/1_15.png"),
          link: "/status_menagement_list", hide_border:true
        }
      ],
      list3: [
        {
          text: "设备信息管理",
          src: require("../../../../assets/images/20_03.png"),
          link: "/device_info_list"
        },
        {
          text: "设备库存明细",
          src: require("../../../../assets/images/20_03.png"),
          link: "/device_details_list"
        }
      ]
    };
  },
  created: function() {},
  methods: {}
};



