import edit_input from "../../../edit_input";
import f_button from "../../../button";
export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      edit_input_list: [
        { read: true, v: "归属信息", k: "京广高铁工务段信阳东站" }
      ],
      edit_input_list1: [
        { read: true, v: "编号", k: "001" },
        { read: true, v: "打卡日期", k: "2019-01-01" },
        { read: true, v: "开始打卡时间", k: "2019-01-01 09:00:12" },
        { read: true, v: "结束打卡时间", k: "2019-01-01 09:00:12" },
        { read: true, v: "作业时长", k: "1时20分35秒" },
        { read: true, v: "打卡轨迹", k: "点击查看" }
      ],
      edit_input_list2: [
        { read: true, v: "开始打卡时间", k: "2019-01-01 09:00:12" },
        { read: true, v: "经纬度", k: "127.84,45,451" },
        { read: true, v: "位置", k: "信阳南站南广场" }
      ],
      edit_input_list3: [
        { read: true, v: "备注", k: "备注信息" },
      ],
      edit_input_list4: [
        { read: true, v: "编号", k: "EO1809231814196051" },
        { read: true, v: "日报日期", k: "2019-01-12" },
        { read: true, v: "关联时间", k: "2019-01-12 16:47:22" }
      ],
      button_list: [
        {
          type: "primary",
          text: "返回",
          call_back: true
        }
      ]
    };
  },
  methods: {
    get_call_back() {
      this.go_page("/clock_in");
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



