import edit_input from "../../../edit_input";
import f_button from "../../../button";
export default {
  components: {
    edit_input,
    f_button,
  },
  data() {
    return {
      edit_input_list: [
        { read: true, v: " ", k: " " },
        { read: true, v: "归属信息", k: "京广高铁工务段信阳东站" }
      ],
      edit_input_list1: [
        { read: true, v: "编号", k: "001" },
        { read: true, v: "上线时间", k: "2019-01-01" },
        { read: true, v: "下线时间", k: "2019-01-01" },
        { read: true, v: " ", k: " " }
      ],
      edit_input_list2: [
        { read: true, v: "作业类型", k: "非上线计划" },
        { read: true, v: "是否天窗", k: "是" },
        { read: true, v: "工作类型", k: "跟车作业" },
        { read: true, v: "工作路局", k: "武汉" },
        { read: true, v: "工作地点", k: "京广高铁工务段信阳东站" },
        { read: true, v: "详细位置", k: "站南5公里" },
        { read: true, v: "行别", k: "上行" },
        { read: true, v: "天气", k: "晴" },
        { read: true, v: "备注", k: "京广高铁工务段信阳东站" }
      ],
      edit_input_list3: [
        { read: true, v: "编号", k: "EO1809231814196051" },
        { read: true, v: "日报日期", k: "2019-01-12" },
        { read: true, v: "关联时间", k: "2019-01-12 12:12:23" },
      ],
      edit_input_list4: [
        { read: true, v: "创建时间", k: "2019-01-12 16:47:22" },
        { read: true, v: "最后更新", k: "2019-01-12 16:47:22" },
      ],
      button_list:[
          {
              type:'primary',
              text:'编辑计划',
              call_back:true
          }
      ],
    };
  },
  methods: {
      get_call_back(){
          this.go_page('/on_plan');
      },
      go_page(url){
          this.$router.push(url)
      }
  },
};



