
import list_item from "../../../../list_item";
import f_button from "../../../../button";
import edit_time from "../../../../edit_time";
import f_search from "../../../../search";
import edit_radio from "../../../../edit_radio";
import Popup from 'vux/src/components/popup/index.vue'

import f_nav_bar from "../../../../nav_bar";
export default {
