},
data() {
return {
search:{
placeholder:'请输入要搜索的设备/人员'
},
slider_bar_show: false,
edit_time_list: [
{ title: "开始日期", default_time: "请选择起始日期" },
{ title: "结束日期", default_time: "请选择结束时间" }
],
edit_radio: { title: "入库类型", list: ["归还", "新增"] },
button_list: [
{
type: "primary",
text: "入库",
call_back: true
}
],
nav_bar: {
list: [
{
cur: true,
text: "入库列表"
}
]
},

list: [
{
title: "2019-01-05",
type: "归还",
time: "2019-01-05 12:12:21",
status: "3",
status_type: 2,
link: "/device_sm_details"
},
{
title: "2019-01-05",
type: "新增",
time: "2019-01-05 12:12:21",
status: "3",
status_type: 2,
link: "/device_sm_details"
},
{
title: "2019-01-05",
type: "新增",
time: "2019-01-05 12:12:21",
status: "3",
status_type: 2,
link: "/device_sm_details",
hide_border:true
}
]
};
},
methods: {
call_back(res) {
this.go_page("/add_device");
},

go_page(url) {
this.$router.push(url);
}
}
};



