import work_bench_item from "../../work_bench_item";
import work_bench_top from "../../work_bench_top";
import tabbar from "../../tabbar";
export default {
  name: "work_bench",
  components: {
    work_bench_item,
    work_bench_top,
    tabbar
  },
  data() {
    return {
      work_bench_top: {
        img: require("../../../assets/images/4.png"),
        name: "张三",
        title: "京广高铁工务段信阳南站",
        title1: "打磨组 组长"
      },
      list: [
        {
          text: "当日完成",
          src: require("../../../assets/images/3_03.png"),
          right_text: "4",
          link: "/report_today"
        },
        {
          text: "次日计划",
          src: require("../../../assets/images/3_03.png"),
          right_text: "3",
          link: "/report_tomorrow"
        },
        {
          text: "作业打卡",
          src: require("../../../assets/images/3_03.png"),
          right_text: "1",
          link: "/clock_in",
          hide_border: true
        }
      ],
      list1: [
        {
          text: "设备管理",
          src: require("../../../assets/images/3_03.png"),
          link: "/device_menagement",
          hide_border: true
        }
      ],
      list2: [
        {
          text: "视频会议",
          src: require("../../../assets/images/1_15.png"),
          link: "/video_meeting"
        },
        {
          text: "知识库",
          src: require("../../../assets/images/1_15.png"),
          link: "/know_how_list",
          hide_border: true
        }
      ],
      list3: [
        {
          text: "通知公告",
          src: require("../../../assets/images/20_03.png"),
          link: "/notice_list",
          right_text: 1
        }
      ]
    };
  },
  created: function() {},
  methods: {}
};



