import list_item from "../../../../list_item";
import f_button from "../../../../button";
import f_search from "../../../../search";
import f_nav_bar from "../../../../nav_bar";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    f_search,
    f_nav_bar
  },
  data() {
    return {
      search: {
        placeholder: "请输入要盘点的日期"
      },
      button_list: [
        {
          type: "primary",
          text: "盘点",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            cur: true,
            text: "盘点列表"
          }
        ]
      },

      list: [
        {
          title: "运营中心",
          type: "12/53",
          time: "2019-01-05",
          status: "进行中",
          status_color: "red",
          link: "/check_info"
        },
        {
          title: "运营中心",
          type: "12/53",
          time: "2019-01-05",
          status: "已完成",
          status_color: "blue",
          link: "/check_info"
        },
        {
          title: "运营中心",
          type: "12/53",
          time: "2019-01-05",
          status: "进行中",
          status_color: "red",
          link: "/check_info",
          hide_border:true
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      // this.go_page("/add_device_out");
    },

    go_page(url) {
      this.$router.push(url);
    }
  }
};



