import edit_input from "../../../../edit_input";
import f_button from "../../../../button";
import edit_time from "../../../../edit_time";
import edit_select from "../../../../edit_select";
import upload_file from "../../../../upload_file";
import nav_bar from "../../../../nav_bar";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
export default {
  components: {
    top_progress,
    edit_input,
    nav_bar,
    edit_time,
    upload_file,
    f_button,
    edit_select,
    f_confirm,
    result,
  },
  data() {
    return {
      nav_bar_idx: -1,
      top_progress: {
        step: 0,
        list: [{ des: "基本信息" }, { des: "详细信息" }, { des: "新增完成" }]
      },
      edit_input_list: [{ required: true, v: "设备名称", k: "请输入设备名称" }],
      edit_input_list1: [
        { required: true, v: "固资编号", k: "请输入固资编号" },
        { required: true, v: "财务卡片号", k: "请输入财务卡片号" },
        { required: true, v: "研究院编号", k: "请输入研究院编号" },
        { v: "备注", k: "请输入备注信息" , hide_border:true}
      ],
      edit_input_list2: [
        { v: "固定资产名称", k: "请输入固定资产名称" },
        { v: "规格型号", k: "请输入规格型号" },
        { v: "机身唯一代码", k: "请输入机身唯一代码" },
        { v: "二维码", k: "绑定", read: true , hide_border:true}
      ],
      edit_input_list3: [{ v: "折旧剩余周期", k: "请输入折旧剩余周期" }],
      edit_input_list4: [
        { v: "质保期（天）", k: "请输入质保天数" },
        { v: "质保到期日", k: "请输入质保天数" , hide_border:true}
      ],
      edit_input_list5: [
        { v: "原值", k: "请输入原值" },
        { v: "每月折旧额", k: "请输入每月折旧额", hide_border:true }
      ],
      upload_file_info: {
        text: "设备照片",
        required: true,
        class: "img40",
      },
      edit_time_list: [
        { title: "资本化日期", default_time: "请选择资本化日期" }
      ],
      edit_time_list1: [
        { title: "质保开始日期", default_time: "请输入开始日期" },
        { title: "质保结束日期", default_time: "请输入结束日期" }
      ],
      button_list: [
        {
          type: "primary",
          text: "下一步",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            text: "仪器信息",
            cur: true
          },
          {
            text: "节点信息",
            cur: false
          },
          {
            text: "财务信息",
            cur: false
          },
          {
            text: "归口信息",
            cur: false
          }
        ]
      },
      edit_select: [
        {
          title: "增加方式",
          default_val: "",
          required: true,
          placeholder: "请选择增加方式",
          list: [
            { key: "增加方式", value: "增加方式" },
            { key: "增加方式1", value: "增加方式1" }
          ]
        },
        {
          title: "利润中心",
          default_val: "",
          required: true,
          placeholder: "请选择利润中心",
          list: [
            { key: "利润中心", value: "利润中心" },
            { key: "利润中心1", value: "利润中心1" }
          ]
        },
        {
          title: "部门",
          default_val: "",
          required: true,
          placeholder: "请选择部门",
          list: [
            { key: "部门1", value: "部门1" },
            { key: "部门", value: "部门" }
          ]
        },
        {
          title: "责任人",
          default_val: "",
          required: true,
          placeholder: "请选择责任人",
          list: [
            { key: "责任人", value: "责任人" },
            { key: "责任人1", value: "责任人1" }
          ]
        },
        {
          title: "使用人",
          default_val: "",
          required: true,
          placeholder: "请选择使用人",
          list: [
            { key: "使用人", value: "使用人" },
            { key: "使用人1", value: "使用人1" }
          ]
        }
      ],
      edit_time_list2: [
        { title: "验收领用日期", default_time: "请输入领用日期" }
      ],
      edit_input_list6: [{ v: "供应商名称", k: "请输入供应商名称" , hide_border:true}],
      confirm_info:{
        title:'新增设备',
        content:'新增设备成功，是否提交保存',
        show:false
      },
      result_info: {
        title: "提交成功！",
        des:
          '编号为<a href="javascript:void(0);">EO1809231814196051</a>的车轮廓形仪新增成功。',
        buttons: [
          {
            type: "primary",
            text: "重新编辑",
            link: "/",
          },
          {
            type: "primary",
            text: "新增设备",
            link: "/"
          },
          {
            type: "primary",
            text: "返回工作台",
            link: "/"
          },
          {
            type: "default",
            text: "关闭",
            link: "/"
          }
        ]
      },
    };
  },
  methods: {
    get_nav_bar(idx) {
      this.nav_bar_idx = idx;
      for (var i in this.nav_bar.list) {
        this.nav_bar.list[i].cur = false;
      }
      if (idx == 3) {
        this.button_list[0].text = "保存";
      } else {
        this.button_list[0].text = "下一步";
      }
      this.nav_bar.list[idx].cur = true;
    },
    get_call_back() {
      var text_val = this.button_list[0].text;
      if (text_val != "保存") {
        this.top_progress.step = 1;
        if (this.top_progress.step == 1 && this.nav_bar_idx < 3) {
          this.nav_bar_idx = this.nav_bar_idx += 1;

          this.nav_bar.list[this.nav_bar_idx].cur = true;
          if (this.nav_bar_idx == 3) {
            this.button_list[0].text = "保存";
          } else {
            this.button_list[0].text = "下一步";
          }
        }
      }else{
        this.confirm_info.show=true
      }
    },
    er_code_back(idx) {
      if (idx == 3) {
        console.log("二维码绑定");
      }
    },
    confirm_back(res){
     this.top_progress.step=2
    },
    go_page(url){
      this.$router.push(url)
    }
  }
};



